.video-playing-menu{
    position: relative;
    background-color: rgb(0, 0, 0);
    overflow: hidden;
    width: 191px;
    height: 125px;
    margin-left: -1px;
  


}
#display-video{
    margin-top:12px;
    margin-left: 1px;

    border-radius: 10px;
    width: 188px;
    height: 100px;
}

#video-tittle{
    margin-top: 20px;
    font-size: .7rem;
    text-overflow: clip;
    overflow: hidden;
    white-space: nowrap;
    bottom:0px;
    width: 190px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    color:white;
    background-color: hsla(192,50.2%,52.0%,0.8);

}

#video-play-icon{
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    width: 190px;
    height: 125px;
    background: rgb(198,198,198);
    background: radial-gradient(circle, rgba(198,198,198,0.6474964985994398) 0%, rgba(117,117,117,0.6895133053221288) 13%, rgba(0,0,0,0.7315301120448179) 70%);

}
#video-play-icon img{
    width: 50px;
    height: 50px;
}


