/* details about the working */
.about{
    top:0;
    position: absolute;
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease-in-out;
    color: white;
    padding: 10px 0px;
    width: 100%;
}

p{
    margin: 2px;
}
ul{
    list-style-position: outside;
}

.info-div{
    border: 3px solid black;
    box-sizing: border-box;
    line-height: 20px;
    font-size: 16px;
    max-height: 700px;
    overflow-y: scroll;
    background-color: #ffa69e;
    background-image: linear-gradient(315deg, #ffa69e 0%, #5d4954 74%);
    transition: all 0.3s ease-in-out;
}

#info-btn{
   
    border-radius: 50%;
    cursor: pointer;
    padding: 5px;
    box-shadow:  0 0 5px 0px rgb(0, 0, 0);

  
}
fieldset{
    font-size: 0.9rem;
   margin-bottom: 10px;

}
#info-btn img{
    width: 20px;
    height: 20px;
}


#info-btn:hover{
    box-shadow: inset 0 0 5px 0px rgb(0, 0, 0);

}